.Con{
    background-color: rgb(211, 211, 211);
    display: grid;
    grid-template-columns: 1fr 2fr 3fr 3fr 1fr;
    align-items: baseline;
    padding: 10px;
   z-index: 20;
}
.navigations a{
    text-decoration: none;
    
    padding-left:1.5rem ;
}



.brand_logo{
    width: 4rem;
    
}
.line{
    
    padding: 0px;
    margin: 0px;
    color: white;

}
.Logo_box{
    grid-column: 2/3;

}
.navigations p{
    padding-left: 1.5rem;
    color: black;
    font-weight: 500;
    

    /* padding-right: 0.4rem; */
}

.navigations p:hover{
    /* padding: 0.2rem; */
    color: black;
    /* font-weight: 500; */
    /* text-decoration:underline; */
    cursor: pointer;
}
.navigations{

    grid-column: 4/5;
    display: flex;
    justify-content: space-between;
    margin: auto;
}

@media (max-width: 730px) {
    .navigations a{
        text-decoration: none;
        padding-left:0.5rem ;
    }
    .navigations p{
        /* text-decoration: none; */
        padding-left:0.5rem ;
    }
 
}
@media (max-width: 404px) {
    .navigations p{
        font-size: 0.9rem;
    }
    .brand_logo{
        width: 3rem;
        
    }
}




/* Hambergure manie */

.Hamnac{
    display: grid;
    /* justify-content: space-evenly; */
    grid-template-columns: 1fr 2fr 2fr 1fr;
    position: sticky;
    top:0px;
    z-index: 10;

    
}
.hambargur_comp{
