.about_profile{

    margin-top: 5rem;
   display: grid;
   grid-template-columns: 2fr 3fr;
   column-gap: 1rem;
   align-items: center;
}
.Image_con{
/* background-color: #c8c5c2; */
width: 30rem;
height: 20rem;
}
.profile{
    border:3px solid white;
    width: 100%;
    border-radius: 50%;
}
/* #c8c5c2 */
.resume_button{
    padding: 0.5rem 1.2rem;
    border-radius: 8px;
    border: 2px solid orange;
    background: #1a202c;
    color: orange;
    font-weight: 500;
    font-size: 1.2rem;
}
.profile_logo{
    width: 30%;
}
.profile_socile{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-top:1rem;
}
@media (max-width: 854px) {
    .profile_logo{
        width: 50%;
    }
}
@media (max-width: 664px) {
    .profile_logo{
        width: 60%;
    }
}
@media (max-width: 600px) {
    .about_profile{

        /* /* margin-top: 5rem; */
       display: grid; 
       grid-template-rows: 1fr 2fr;
       grid-template-columns: 1fr;
       /* column-gap: 1rem;
       align-items: center; */
    }
    .profile_logo{
        width: 50%;
    }
}






.profile_logo{
    display: block;
    margin: auto;
}