.about_skill div>img{
    width:30%;
    margin: auto;
}
.about_skill div>img:hover{
    width:33%;
}
.about_skill{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap:2rem;
    font-size: 1.2rem;
}

.skill_Head{
    /* text-align: start; */
    font-size: 2rem;
    margin-bottom: 2rem;
    margin-left: 2rem;
    color:aliceblue;
}
.about_skill>div>p{
    color: white;
}

@media(max-width:800px){
    .about_skill{
        grid-template-columns: repeat(3,1fr);
    }
        

}

@media(max-width:600px){
    .about_skill{
        grid-template-columns: repeat(2,1fr);
    }    

}