.about_project div>video{
   width: 35rem;
   margin:auto;
}
.about_project>div{
    margin-top: 1.5rem;
}
.about_project>div:first-child{
    margin-top: 0;

}

.pro_sec_1,.pro_sec_2,.pro_sec_3{
    display: grid;
    grid-template-columns: 2fr 2fr ;
/*     margin-top:10rem; */
    padding: 5rem;
    column-gap:2rem ;
}
.pro_sec_1{
    margin-top: 0rem;
}
.pro_sec_1>div:first-child,.pro_sec_2>div:first-child,.pro_sec_3>div:first-child{
grid-column: 1/2;
}
.pro_sec_1>div:nth-child(2),.pro_sec_2>div:nth-child(2),.pro_sec_3>div:nth-child(2){
grid-column: 2/3;
align-items: center;
    
}
.pro_sec_1>div:nth-child(2)>ul,.pro_sec_2>div:nth-child(2)>ul,.pro_sec_3>div:nth-child(2)>ul{
text-align: start;
margin-top:2rem;
padding-left: 4.1rem;
color: aliceblue;
        
    }
    
.project_view{
    background-color: #1a202c;
  
    border: 2px solid orange;
    border-radius: 0.5rem;
    padding: 0.4rem 0.7rem;

}
.project_repo{
    background-color: #1a202c;
  
    border: 2px solid orange;
    border-radius: 0.5rem;
    padding: 0.4rem 0.7rem;
    margin-left: 1rem;
}
.project_view>a,.project_repo>a{
    text-decoration: none;
    color: orange;
    font-size: 1.2rem;
}

.WorkStackk{
    display: flex;
    /* align-items: center;
     */
     justify-content: center;
     gap:1rem

}
.WorkStackk_big{
    display: flex;
    /* align-items: center; */
    justify-content: center;
    gap: 1rem;
}
.WorkStackk img{
    width: 4rem;
    padding:1rem
}
.WorkStackk_big img{
    width: 4rem;
    padding:1rem
}

.stackHerad{
    padding-top: 1rem;
    text-align: center;
    font-size: 1.2rem;
    
}

.Box1_project{
    border: 2px solid orange;
    /* box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #f0f, 0 0 40px #0ff, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073; */
    border-radius: 0.7rem;
}

    @media (max-width: 1140px) {
        .pro_sec_1,.pro_sec_2,.pro_sec_3{
            display: block;
            margin-top:7rem;
            padding: 2rem;
       
        }
    }

    @media (max-width: 600px) {
        .about_project div>video{
            width: 30rem;
       

         }
         .pro_sec_1>div:nth-child(2)>ul,.pro_sec_2>div:nth-child(2)>ul,.pro_sec_3>div:nth-child(2)>ul{
            text-align: start;
            margin-top:2rem;
padding-left: 0.2rem;

            
                    
                }
         .pro_sec_1,.pro_sec_2,.pro_sec_3{
      
            padding: 1rem;
       
        }
        .Box1_project{
            border: none
            
        }
        .WorkStackk_big img{
            width: 2.8rem;
            padding:0
        }
        .WorkStackk_big{
            display: flex;
            align-items: baseline;
            justify-content: center;
            gap: 1rem;
        }
    }

    @media (max-width: 500px) {
        .about_project div>video{
            width: 23rem;
     

         }
         .pro_sec_1,.pro_sec_2,.pro_sec_3{
         border: none;
            padding: 0rem;
       
        }
        .Box1_project{
            border: none
            
        }
        .WorkStackk_big{
            display: grid;
            grid-template-columns: 1fr 1fr;
             justify-items: start;
             gap:1rem
        }
        
    }
